.input-field {
    position: relative;
    width: 100%;
    height: 64px;
    background-color: #edf4ff;
    border-radius: 20px;
    border: 1px #B2EAEE solid;
    margin-bottom: 24px;
}

.input-field__text {
    background-color: #edf4ff;
    font-weight: 400;
    width: 100%;
    height: 100%;
    resize: none;
    padding: 20px;
    line-height: 18px;
    font-size: 16px;
    border-radius: 20px;
}

.btn {
    position: absolute;
    top: 18px;
    right: 10px;
    width: 30px;
    height: 25px;
    background-color: #edf4ff;
    border-radius: 5px;
    border: 1px brown solid;
}